@import './imports/reset.css';

html {
    width: 100%;
    height: 100%;
    scroll-behavior: smooth;
}

p {
    margin: 10px;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 62.5%;
    height: 100%;
    line-height: 1;
    margin: 0 auto;
    overflow-x: auto;
    overflow-y: scroll;
    padding: 0;
    background: url(../images/img.jpg);
    background-size: cover;
    background-attachment: fixed;
    box-sizing: border-box;
}